import React from 'react';
import {Card, CardColumns, Button, Nav, Row,Col, InputGroup, Form} from 'react-bootstrap';





//–∑–∞–¥–∞–Ω–∏–µ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ —á–µ—Ä–µ–∑ –∫–ª–∞—Å—Å
class List extends React.Component {
  constructor(props) {
  		super(props); //–≤—ã–∑–≤–∞—Ç—å –∫–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä –Ω–∞—Å–ª–µ–¥—É–µ–º–æ–≥–æ –∫–ª–∞—Å—Å–∞, —Ç.–µ. React.Component



      //let tasks =  localStorage.getItem('tasks')? JSON.parse(localStorage.getItem('tasks')) :   [{text:"—Å–æ–∑–¥–∞—Ç—å –ø–µ—Ä–≤—É—é –∑–∞–¥–∞—á—É", kind:'ü¶ü', important: 1}];
      let tasks =[{text:"—Å–æ–∑–¥–∞—Ç—å –ø–µ—Ä–≤—É—é –∑–∞–¥–∞—á—É", kind:'ü¶ü', important: 0},{text:"—Å–æ–∑–¥–∞—Ç—å –ø–µ—Ä–≤—É—é –∑–∞–¥–∞—á—É", kind:'ü¶ü', important: 1}]
      this.state = {
        edited: null,
        filter:'all',
  			tasks: tasks
        };

      this.input = React.createRef();
      this.important = React.createRef();
      this.kind = React.createRef();
  	}

  saveTask(){
    if(this.state.edited!==null){
      this.setState({edited:null});
    }else{
    if(this.input.current.value==='') return false;
      let new_task ={
        'text':  this.input.current.value,
        'important': this.important.current.value,
        'kind': this.kind.current.value,
      }
      console.log(new_task);
      let tasks = this.state.tasks.slice();
      tasks.unshift(new_task);
      localStorage.setItem('tasks', JSON.stringify(tasks));
      this.setState({tasks});
    }
    this.input.current.value="";
    this.important.current.value="‚òÜ";
    this.kind.current.value="ü¶ü";
  }

  changeState(key, value){
      if(key === 'filter'){
        let filter = value;
        this.setState({filter});
      }else if(key === 'edited'){
        let edited = value;
        this.setState({edited});
      }

    }

  setTaskField(id,field,value){
    let tasks = this.state.tasks.slice();
    if(field === 'delete'){
      tasks.splice(id,1);
    }else{
      tasks[id][field] = value;
    }
    localStorage.setItem('tasks', JSON.stringify(tasks));
    this.setState({tasks});

  }


  render(){

      let list = this.state.tasks.map((item,index) => {
        if(this.state.filter === 'important' && !item.important) {return false;}
        else if(this.state.filter === 'plans' && item.finished) {return false;}
        else if(this.state.filter === 'finished' && !item.finished) {return false;}
        return (
        <Card key={index} border={item.important && 'danger'} text={item.finished && 'success'} bg={item.finished && 'light'}>
          <Card.Header className="py-1">
            <div className="float-left emoji" >{item.kind}</div>
            <div className="importantBlock float-right emoji" onClick={()=>this.setTaskField(index,'important', !item.important)}>{item.important  ? '‚≠ê'  : '‚òÜ' }</div>
            <div className="importantBlock float-right emoji" onClick={()=>this.setState({edited:index})}> ‚úè </div>


          </Card.Header>
          <Card.Body >
            <Card.Text>{item.text}</Card.Text>
            <Button size="sm" variant="danger" className="float-left emoji mb-1" onClick={()=>this.setTaskField(index,'delete', true)}><span role="img" aria-label="ok">üóë</span></Button>

            <Button size="sm" variant="success" className="float-right emoji mb-1" onClick={()=>this.setTaskField(index,'finished', !item.finished)}><span role="img" aria-label="ok">üëå</span></Button>
          </Card.Body>
        </Card>
      )});

    let taskEdited=(this.state.edited!==null)?this.state.tasks[this.state.edited] : {important:'',kind:''};


    return (

      <>
      <Row>


        <Col  md="8" className="py-3">
          <Form.Control placeholder='—Ç–µ–∫—Å—Ç –Ω–æ–≤–æ–π –∑–∞–¥–∞—á–∏'
            value={taskEdited.text? taskEdited.text: null}
            ref={this.input}
            onChange={(e)=>{this.state.edited!==null && this.setTaskField(this.state.edited,'text', e.target.value)} }
          />
          </Col>
          <Col  md="4" className="py-3">
          <InputGroup>
            <InputGroup.Append>
            <Form.Control as="select" custom
              ref={this.important}
              onChange={(e)=>(this.state.edited!==null && this.setTaskField(this.state.edited,'important', (e.target.value=='‚≠ê'?1:0)))}
              >
              <option >‚òÜ</option>
              <option selected={taskEdited.important && true}>‚≠ê</option>
            </Form.Control>
              <Form.Control as="select" custom
                ref={this.kind}
                onChange={(e)=>(this.state.edited!==null && this.setTaskField(this.state.edited,'kind', e.target.value))}
              >
                <option selected={taskEdited.kind=='ü¶ü' && 'selected'}>ü¶ü</option>
                <option selected={taskEdited.kind=='üåï' && 'selected'}>üåï</option>
                <option selected={taskEdited.kind=='üå∑' && 'selected'}>üå∑</option>
                <option selected={taskEdited.kind=='‚Åâ' && 'selected'}>‚Åâ</option>
                <option selected={taskEdited.kind=='ü¶Ñ' && 'selected'} >ü¶Ñ</option>
              </Form.Control>
              <Button  onClick={()=>this.saveTask()}>—Å–æ—Ö—Ä.</Button>
            </InputGroup.Append>
            </InputGroup>
            </Col>


      </Row>
      <Row>
        <Col>
         <Nav variant="tabs" defaultActiveKey="#all">
          <Nav.Item>
            <Nav.Link href="#all" onClick={()=>this.changeState('filter','all')}> –í—Å–µ </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link href="#important" onClick={()=>this.changeState('filter','important')}> –í–∞–∂–Ω—ã–µ </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link href="#plans" onClick={()=>this.changeState('filter','plans')}> –ü–ª–∞–Ω–∏—Ä—É–µ–º—ã–µ</Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link href="#finished" onClick={()=>this.changeState('filter','finished')}> –°–¥–µ–ª–∞–Ω–Ω—ã–µ </Nav.Link>
          </Nav.Item>
        </Nav>
        </Col>
      </Row>
      <Row>
         <CardColumns className="m-3">
           {list}
        </CardColumns>
      </Row>
      </>
       );
     }

}

export default List;
